@import 'utils/variables', 'utils/mixins';

.modal {
  width: 1556px;
  min-height: Min(722px, calc(100% - 48px));
  display: flex;
  flex-direction: column;

  .body {
    flex: 1;
    max-width: 100%;
    padding-block: 8px;
    display: flex;
    flex-direction: column;
  }
}

.loader {
  margin-top: 20px;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.filterRow {
  display: flex;
  padding: 8px 24px;
  gap: 8px;
}

.resetFilter {
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid $dark-blue;
  border-radius: 2px;
  position: relative;
  width: 40px;

  @include darkTheme {
    border: 1px solid $dark-white;
  }
}

.filterCross {
  width: 8px;
  height: 8px;
  position: absolute;
  right: 4px;
  bottom: 4px;

  path {
    stroke: #f2696d;
    stroke-width: 4px;
  }
}

.bottomBar {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding: 16px 24px;
  border-top: 1px solid $base-gray;
}

.footer {
  display: flex;
  justify-content: flex-end;
}
