@import 'utils/variables', 'utils/mixins';

.container {
  position: absolute;
  padding: 4px;
  background: white;
  box-shadow: 0px 16px 37px 0px rgba(0, 0, 0, 0.16);
  border-radius: 4px;
  left: 4px;
  top: calc(100% - 4px);
  z-index: 101;
  display: flex;
  align-items: center;
  gap: 4px;

  input {
    height: 30px;
    width: 180px;
    padding-inline: 7px;
  }

  .dropdown {
    width: 100px;
    height: 30px;
    font-weight: 400;
  }

  .dash {
    font-size: 12px;
  }

  @include darkTheme {
    background: $light-black;
    color: $dark-white;
  }
}
