@import 'utils/variables', 'utils/mixins';

.tableMain {
  flex: 1;
  padding-inline: 24px;
  font-size: 14px;
  display: flex;
  flex-direction: column;
}

.table_container {
  @include scrollbar;
  flex: 1;
  position: relative;
  overflow: scroll auto;
  padding-top: 8px;
  max-height: calc(100vh - 308px);
}

.data_table {
  width: max-content;

  th {
    border-top: 1px solid $base-gray;
    position: relative;

    &:first-child {
      border-top-left-radius: 4px;
    }

    &:last-child {
      border-top-right-radius: 4px;

      & > div:last-child {
        left: unset;
        right: 4px;
      }
    }

    & > svg {
      position: absolute;
      right: 16px;
      bottom: 10px;
    }
  }

  th,
  td {
    border-bottom: 1px solid $base-gray;
    border-right: 1px solid $base-gray;
    max-width: 400px;

    &:first-child {
      border-left: 1px solid $base-gray;
    }
  }

  tr {
    &[data-selected='true'] {
      td {
        background-color: #f3f4f7;
      }
    }

    &:last-child {
      td {
        &:first-child {
          border-bottom-left-radius: 4px;
        }

        &:last-child {
          border-bottom-right-radius: 4px;
        }
      }
    }
  }
}

.data_table_header {
  text-align: left;
  display: contents;
  color: $base-black;
  font-weight: 700;

  @include darkTheme {
    color: $dark-white;
  }
}

.data_table_header {
  th,
  td {
    height: 40px;
    vertical-align: bottom;
    padding: 8px 16px;

    &:not(:first-of-type) {
      cursor: pointer;

      &:hover,
      &[data-active='true'] {
        background-color: rgba($base-gray, 0.25);
      }
    }

    & > span {
      display: block;
      margin-right: 26px;
      line-height: 18px;
    }
  }
}

.data_table_tbody {
  color: #2a2f42;
  font-weight: 100;
  position: relative;
}

.data_table_tbody {
  th,
  td {
    height: 40px;
    padding: 8px 16px;
    line-height: 15px;
    font-weight: 400;
    color: $base-black;

    @include darkTheme {
      color: $dark-white !important;
    }

    div {
      display: flex;
      align-items: center;
      max-width: 180px;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      justify-content: center;
      gap: 8px;

      & > img {
        width: 18px;
        height: 18px;
      }

      &.indicators {
        max-width: none;
      }
    }

    &:nth-child(9) {
      max-width: none;
    }
  }
}

.data_table_checkbox {
  position: sticky !important;
  left: 0;
  background: white;
  z-index: 100;
  transform: translate3d(0, 0, 0);

  svg {
    cursor: pointer;
  }

  @include darkTheme {
    background: $light-black;
  }
}

.paginationWrapper {
  display: flex;
  justify-content: flex-start;
  position: sticky;
  left: 0;
  height: 34px;
  margin-block: 8px;
}

button {
  &.active {
    background: $middle-blue;
    color: white;
  }
}

.pagination {
  display: flex;
  align-items: center;
  width: 100%;

  button {
    font-size: 14px;

    &:disabled {
      cursor: not-allowed;
    }
  }

  @include darkTheme {
    color: $dark-white;
    button,
    span {
      color: $dark-white;
    }
  }
}

.total {
  position: absolute;
  left: 0;
  bottom: 8px;
  font-size: 13px;

  @include darkTheme {
    color: $dark-white;
  }
}

.buttonActive {
  background: #0b346a;
  color: white;

  @include darkTheme {
    background: $middle-blue;
    color: white;
  }
}

.select {
  margin-left: 12px;

  & > div:nth-child(2) {
    min-width: 70px;
    border-radius: 2px;

    @include darkTheme {
      border-color: $dark-white;
    }
  }

  & > div:nth-child(3) {
    top: 0;
    transform: translateY(calc(-100% + -7px));

    @include darkTheme {
      background: $light-black;
      color: $dark-white;
    }
  }
}

.checked {
  position: absolute;
  left: 140px;
  bottom: 8px;
  font-size: 13px;

  @include darkTheme {
    color: $dark-white;
  }
}

.alignRight {
  text-align: right;
  width: 200px;
}

.alignCenter {
  text-align: center;
}

.editButton {
  all: unset;
  cursor: pointer;
  width: 18px;
  height: 18px;
  margin-bottom: 4px;

  &:hover {
    filter: brightness(1.75);
  }

  & > svg {
    display: block;
    margin: auto;
    width: 12px;
    height: 12px;
  }
}

.fullText {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.indicatorsIcons {
  width: 18px !important;
}

.round {
  width: 18px;
  height: 18px;
  border-radius: 50%;
}

.green {
  border: 2px solid #2aae6b;
  background-color: rgba(42, 174, 107, 0.1);
}

.yellow {
  border: 2px solid #e4df1a;
  background-color: rgba(228, 223, 26, 0.1);
}

.red {
  border: 2px solid #ae0d0b;
  background-color: rgba(174, 13, 11, 0.1);
}

.gray {
  border: 2px solid #adacae;
  background-color: rgba(134, 128, 128, 0.1);
}

.integral {
  align-items: center;
  max-width: none;
}
