@import 'utils/variables', 'utils/mixins';

.container {
  position: absolute;
  padding: 0px;
  background: white;
  box-shadow: 0px 16px 37px 0px rgba(0, 0, 0, 0.16);
  border-radius: 4px;
  left: 4px;
  top: calc(100% - 4px);
  z-index: 101;
  padding: 4px;

  input {
    height: 30px;
    width: 180px;
    padding-inline: 7px;
  }

  @include darkTheme {
    background: $light-black;
    color: $dark-white;
  }
}
