@import 'utils/variables', 'utils/mixins';

.container {
  position: absolute;
  background: white;
  box-shadow: 0px 16px 37px 0px rgba(0, 0, 0, 0.16);
  left: 4px;
  top: calc(100% - 4px);
  z-index: 101;
  overflow: auto;
  border-radius: 4px;
  padding-block: 4px;
  font-weight: normal;
  max-height: 300px;
  max-width: 400px;
  width: max-content;

  @include darkTheme {
    background: $light-black;
    color: $dark-white;
  }
}

.listItem {
  list-style: none;
  user-select: none;
  color: #4f4f4f;
  padding: 0 10px;

  & > label {
    cursor: pointer;
    display: flex;
    gap: 8px;
    flex-flow: row nowrap;
    align-items: center;
    min-height: 30px;
    padding-block: 4px;

    & > span {
      display: inline-block;
      line-height: 18px;
    }

    & > img {
      width: 18px;
      height: 18px;

      &:not([data-size='lg']) {
        width: 16px;
        height: 16px;
        padding: 1px;
      }
    }
  }

  &:hover,
  &[data-selected='true'] {
    background-color: #f3f4f7;

    @include darkTheme {
      color: $light-black;
    }
  }

  @include darkTheme {
    color: $dark-white;
  }
}

.round {
  width: 16px;
  height: 16px;
  border-radius: 50%;
  padding: 1px;
}
