@import 'utils/variables', 'utils/mixins';

.container {
  display: flex;
  flex-flow: row wrap;
  align-items: flex-end;
  flex: 1 0;
  gap: 4px;
}

.filterTag {
  border-radius: 2px;
  height: 30px;
  width: 165px;
  display: flex;
  align-items: center;
  padding: 8px;
  font-size: 12px;
  color: #4f4f4f;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  position: relative;
  gap: 8px;
  background-color: #f3f4f7;

  & > div:first-child {
    width: 14px;
    height: 14px;
  }

  & > span {
    cursor: default;
    display: inline-block;
    text-overflow: ellipsis;
    overflow: hidden;
    width: 105px;

    &:first-child {
      width: 127px;
    }
  }

  & > img {
    width: 14px;
    height: 14px;
  }
}

.crossIcon {
  cursor: pointer;
  position: absolute;
  right: 0;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 30px;

  &:hover {
    color: $red;
  }
}
